<template>

    <div>
        <form class="text-start">
            <div class="mb-3">
                <label for="inputNombre" class="form-label">Nombre</label>
                <input type="text" class="form-control" id="inputNombre" v-model="nombre">
            </div>
            <div class="mb-3">
                <label for="inputApellido" class="form-label">Apellido</label>
                <input type="text" class="form-control" id="inputApellido" v-model="apellido">
            </div>
            <button class="btn btn-success" @click.prevent="resul()">resul</button>

        </form>
    </div>

</template>

<script>

import {mapState, mapMutations} from 'vuex'

export default {
    name: 'Formulario',
    data(){
        return{
            nombre: '',
            apellido: ''
        }
    },
    computed:{
        ...mapState(['datos'])
    },
    methods:{
        ...mapMutations(['agregar']),
        resul(){   
            let id = this.datos.length + 1    
            let obj = {i:id, n:this.nombre,a:this.apellido}; //obj sera el parametro que se envia a store
            this.agregar(obj)        //este parametro lo recibe payload
        }
    }
}
</script>

