import { createStore } from 'vuex'

export default createStore({
  state: {
    datos:[]
  },
  mutations: {
    agregar(state, payload){
      state.datos.push(payload) //se pushea payload en array 'datos' 
      console.log('array',state.datos)
      console.log('payload',payload)//payload viene como parametro de la funcion desde el componente
    }
  },
  actions: {//Para este ejemplo no es necesario una accion de la mutacion
    Agregar({ commit }) {
      commit('agregar')
    }
  },
  modules: {
  }
})
