<template>
    <h1>Lista de Estudiantes VUE.JS</h1>
    <div>
        <table class="table">
            <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellido</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="dato of datos" :key="dato">
                    <th scope="row">{{dato.i}}</th>
                    <td>{{dato.n}}</td>
                    <td>{{dato.a}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>

import {mapState} from 'vuex';

export default {
    computed:{
        ...mapState(['datos'])
    }
}
</script>

